.settings-panel {
  display: block;
  position: fixed;
  top: 0;
  right: -300px;
  bottom: 0;
  width: 300px;
  height: 100vh;
  min-height: 100%;
  background: #fff;
  padding-top: 50px;
  transition-duration: .25s;
  transition-timing-function: ease;
  transition-property: right, box-shadow;
  z-index: 9999;
}

.settings-panel .nav-tabs {
  display: flex;
  justify-content: center;
  width: auto;
  margin: 0;
  padding: 0;
  background: linear-gradient(120deg, #556cdc, #128bfc, #18bef1);
}

.settings-panel .nav-tabs .nav-item {
  border: none;
}

.settings-panel .nav-tabs .nav-item .nav-link {
  text-align: center;
  border: none;
  display: flex;
  align-items: center;
  color: rgba(255,255,255,0.5);
  transition-duration: 0.4s;
  transition-property: color;
  justify-content: center;
}

.settings-panel .nav-tabs .nav-item .nav-link.active {
  background: transparent;
  color: #fff;
}

.settings-panel .tab-content {
  border: none;
  padding: 20px 0 0;
}

.settings-panel .tab-content .tab-pane.scroll-wrapper {
  position: relative;
  max-height: 100vh;
  height: 100%;
  padding-bottom: 150px;
}

.settings-panel .settings-heading {
  padding: 16px 0 13px 20px;
}

.rtl .settings-panel .settings-heading {
  padding: 16px 35px 13px 0;
  text-align: right;
}

.rtl .settings-panel small.settings-heading {
  padding: 16px 0 13px 12px;
}

.settings-panel .sidebar-bg-options {
  padding: 13px 35px;
  display: flex;
  align-items: center;
  font-size: .875rem;
  line-height: 1;
  color: #595959;
  background: #fff;
  transition-duration: .25s;
  transition-property: background;
}

.settings-panel .sidebar-bg-options.selected {
  background: #e6e9ed;
}

.settings-panel .color-tiles {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0 35px 10px;
  padding-top: 15px;
}

.settings-panel .color-tiles .tiles {
  margin: 10px 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.settings-panel .color-tiles .tiles:before {
  content: "";
  width: 0;
  height: 0;
  opacity: 0;
  background: rgba(255,255,255,0.5);
  border-radius: 100%;
  border: 0 solid rgba(255,255,255,0.4);
  transition-duration: .25s;
  transition-timing-function: ease;
}

.settings-panel .color-tiles .tiles.selected:before {
  width: 10px;
  height: 10px;
  opacity: 1;
  border-width: 1px;
}

.settings-panel .color-tiles .tiles.pink {
  background: #e91e63;
}

.settings-panel .color-tiles .tiles.default {
  border: 1px solid #e2e4e7;
  background: linear-gradient(120deg, #556cdc, #128bfc, #18bef1);
}

.settings-panel .chat-list {
  padding-left: 0;
}

.settings-panel .chat-list .list {
  padding: 0.4rem 0.8rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #dee2e6;
}

.settings-panel .chat-list .list:last-child {
  border-bottom: none;
}

.settings-panel .chat-list .list .profile {
  position: relative;
  margin-right: 1rem;
}

.settings-panel .chat-list .list .profile img {
  width: 2.50rem;
  height: 2.50rem;
  border-radius: 100%;
}

.settings-panel .chat-list .list .profile span {
  height: 0.75rem;
  width: 0.75rem;
  position: absolute;
  bottom: 0.34rem;
  right: 0;
  border: 0.13rem solid #fff;
  border-radius: 100%;
}

.settings-panel .chat-list .list .profile span.online {
  background: #19d895;
}

.settings-panel .chat-list .list .profile span.offline {
  background: #ffaf00;
}

.settings-panel .chat-list .list .info {
  margin-right: auto;
}

.settings-panel .chat-list .list .info p {
  display: block;
  margin-bottom: 0;
}

.settings-panel .chat-list .list .info p:last-child {
  opacity: 0.5;
  font-size: 0.8rem;
}

.rtl .settings-panel .chat-list .list .info p:last-child {
  text-align: right;
}

.settings-panel .chat-list .list.active {
  background: #fff;
}

.settings-panel.open {
  right: 0;
  box-shadow: 7px 0 80px -9px rgba(0,0,0,0.2);
}

.settings-panel .settings-close {
  position: absolute;
  top: 47px;
  right: 10px;
  color: #2196f3;
  background: transparent;
  border-radius: 4px;
  padding: 0 3px;
  cursor: pointer;
  transition-duration: 0.2s;
  z-index: 999;
}

.settings-panel .settings-close:hover {
  background: rgba(255,255,255,0.3);
}

.rtl .settings-panel .settings-close {
  right: unset;
  left: 10px;
}

.settings-panel .events p {
  font-family: "Roboto",sans-serif;
}

.rtl .settings-panel .events p {
  text-align: right;
}

.rtl .settings-panel {
  right: unset;
  left: -300px;
  transition-property: left;
}

.rtl .settings-panel .chat-list {
  padding-right: 0;
}

.rtl .settings-panel .chat-list .list .profile {
  margin-right: 0;
  margin-left: 1rem;
}

.rtl .settings-panel .chat-list .list .info {
  margin-right: 0;
  margin-left: auto;
}

.rtl .settings-panel .chat-list .list .info .badge {
  margin-right: 10px;
}

.rtl .settings-panel.open {
  left: 0;
  right: unset;
}

.right-sidebar-toggler-wrapper {
